html {
  background: rgb(0, 95, 194);
  background: rgba(0, 95, 194, 1);
  height: 100%;
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.noselect {
  -webkit-touch-callout: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.delay-470ms {
  -webkit-animation-delay: 0.47s;
  animation-delay: 0.47s;
}

.delay-1300ms {
  -webkit-animation-delay: 1.3s;
  animation-delay: 1.3s;
}

.delay-2500ms {
  -webkit-animation-delay: 2.5s;
  animation-delay: 2.5s;
}
#portfolio-banner {
    height: 25vh;
    background: #005fc2;
}

#banner-title {
    margin-top: 2vh;
    font-size: 14vh;
    margin-bottom: 0px;
}

#banner-subtitle {
    font-size: 4vh;
    margin-bottom: 3vh;
}

#banner-title,
#banner-subtitle {
    font-family: "Amatic SC", cursive;
    color: #e86600;
}

#menu-box-placeholder {
    height: 3vmin;
    background: #ffc800;
}

#menu-bar {
    background: #ffc800;
}

#menu-ul {
    margin-top: 3.5vh;
    margin-left: 2vmin;
}

a.menu-choice {
    font-family: Arial,"Helvetica Neue",Helvetica,sans-serif;
    -webkit-text-decoration: bold;
            text-decoration: bold;
    color: white;
    text-align: center;
}

li::before {
    display: none;
}

#left-menu-item {
    margin-left: 2vmin;
}

.breadcrumb li:first-child a {
    padding-left: 4vmin;
}

a.menu-choice:hover {
    font-family: 'Knewave', cursive;
    color: rgb(245, 136, 154);
    border-radius: 5px;
}
#page-footer {
    background: #ffc800 !important;
    height: 1vmin;
    bottom: 0;
    position: relative;
    width: 100%;
    margin-top: 2vh;
}

.fa-copyright {
    font-size: 13px;
}

.fa-github,
.fa-linkedin {
    font-size: 22px;
}

@-webkit-keyframes bounceLot {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

@keyframes bounceLot {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

.fa-linkedin {
    -webkit-animation: bounceLot 0.7s cubic-bezier(.5,0.05,1,.5);
            animation: bounceLot 0.7s cubic-bezier(.5,0.05,1,.5);;
    -webkit-animation-direction: alternate;;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes bounceLittle {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

@keyframes bounceLittle {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

.fa-github {
    -webkit-animation: bounceLittle 1s cubic-bezier(.5,0.05,1,.5);
            animation: bounceLittle 1s cubic-bezier(.5,0.05,1,.5);;
    -webkit-animation-direction: alternate;;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}


.showcase {
    height: 500px;
    width: 300px;
    margin: 50px auto 50px auto;
}

.project-content {
    height: 270px;
}

.project-links {
    align-items: center;
    display: flex;
    flex-basis: 0;
    flex-grow: 1;
    flex-shrink: 0;
    justify-content: center;
    padding: .75rem;
}

#gh-icon {
    font-size: 3vh;
}

#ext-link-icon {
    font-size: 2.4vh;
}
.app {
    background-color: rgb(247, 147, 238);
}
